
/**
 *
 */
export const lerp = (amount: number, min: number, max: number): number =>
    min + amount * (max - min)

let _isWindowScrollable = true
/**
 *
 */
export const setWindowScrollingEnabled = (enabled: boolean) =>
    _isWindowScrollable = enabled
/**
 *
 */
window.addEventListener(
    "touchmove",
    (e) => {
        if (! _isWindowScrollable) {
            e.preventDefault()
            e.stopPropagation()
        }
    },
    {
        passive: false
    }
)
window.addEventListener(
    "scroll",
    (e) => {
        if (! _isWindowScrollable) {
            e.preventDefault()
            e.stopPropagation()
        }
    },
    {
        passive: false
    }
)
